.key {
    width: 6%;
    position: relative;
    aspect-ratio: 1;
    border-radius: 4px;
    background-color: #ffffff;
    display: inline-block;
    border-style: solid;
    border-width: 1px;
    z-index: 1;
}


.SKey {
    background-color: #9F9F9F;
    align-content: center;
    margin-top: 0px;
}


#TabSK {
    width: 12%;
    aspect-ratio: 2/1;
    top: 50%
}

#BackspaceSK {
    width: 12%;
    aspect-ratio: 2/1;
}

#EnterSK {
    width: 12%;
    aspect-ratio: 2/1;
}

#CapsLockSK {
    width: 12%;
    aspect-ratio: 2/1;
}

#ShiftLeftSK {
    width: 15%;
    aspect-ratio: 2.5/1;
}

#ShiftRightSK {
    width: 15%;
    aspect-ratio: 2.5/1;
}

#keyboardBg{
    width:790px;
    background-color: #EDDBDB;
    border-style: solid;
    border-color: #B6A9A9;
    border-radius: 2px;
    position: relative;
    align-content: center;
    alignment:center;
    left: 25%;
}